@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:400,700');

/**
 * Style variables
 */
$base-font-family: 'Source Code Pro', monospace !default;
$base-font-size: 14.0px !default;
$code-font-size: 14.0px !default;
$mobile-font-size: 12px !default;
$base-line-height: 1.5 !default;
$container-width: 90% !default;
$container-max-width: 600px !default;

/**
 * Global
 */
body { background-color: var(--background-color);  margin: 0 auto;  padding: 0;  font-family: $base-font-family;  font-size: $base-font-size;  color: var(--text-color);  text-align: left;  line-height: $base-line-height !important; }
h1, h2, h3, h4, h5, h6 { font-size: $base-font-size;  margin: 0px;  margin-top: 22px;  font-weight: bold; }
a { text-decoration: underline;  color: var(--link-color); }
a:hover { color: var(--background-color); background-color: var(--base-color); }

/**
 * Layout
 */
.container { width: $container-width;  max-width: $container-max-width;  margin-right: auto;  margin-left: auto; padding: 20px 0; }
p { word-wrap: break-word;  word-break: break-word; margin-bottom: 15px; }
footer { border-top: var(--border);  margin: 20px auto 15px;  padding-top: 10px;  text-align: right; }
header { margin-top: 25px;  margin-bottom: 10px; }

/**
 * Highlight/Markup
 */
::selection { background: var(--selection-background);  color: var(--selection-text); }
::-moz-selection { background: var(--selection-background);  color: var(--selection-text); }

/**
 * Lists
 */
ul { list-style: none; list-style-type: none; padding-left: 0; }
:not(.menu)>ul>li { display: flex; align-items: center; }
:not(.menu)>ul>li:before { content: "-"; margin-right: 9px; }
:not(.menu)>ul>li>a { display: flex; justify-content: space-between; flex-grow: 1; }
:not(.menu)>ul>li>a>span { margin-left: auto; opacity: 0.5; }
:not(.menu)>ul>li>a>span {
    @media (max-width: 600px) {
        display: none;
    }
}

/**
 * Header/Navigation
 */
.menu { border-top: var(--border);  border-bottom: var(--border);  margin-bottom: 25px; }
.menu a { text-decoration: none; }
.menu h1 { margin: 10px 0; display: inline-block; }
.menu ul { margin: 10px 0; position: static; float: right; }
.menu ul li { display: inline;  margin-left: 10px; }

/* footer */
footer a { display: inline-block; height: 18px; }
footer svg { height: 18px; width: 16px; }
footer svg:hover { filter: invert(1); }

